<template>
  <div id="article-add">
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <span class="cdl-form-title"></span>
        <div class="cdl-form-cnt">
          <Input v-model="articleData.title" placeholder=""/>
        </div>
      </div>
    </div>
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <span class="cdl-form-title"></span>
        <div class="cdl-form-cnt">
          <Input v-model="articleData.preface" type="textarea" :rows="4" placeholder=""/>
        </div>
      </div>
    </div>
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <span class="cdl-form-title"></span>
        <div class="cdl-form-cnt">
          <label for="uploadImg" class="cdl-button blue">
            <i class="fa fa-upload"></i>&nbsp;&nbsp;
          </label>
          <input
            type="file"
            accept="image/gif, image/jpeg, image/jpg, image/png, image/svg"
            id="uploadImg"
            @change="uploadImg"
            :value="fileVal"
          >
        </div>
      </div>
      <div id="cover" v-show="articleData.cover">
        <i class="uploadImgClose cdl-close fa fa-close" title="" @click="closeUploadImg"></i>
        <img :src="articleData.cover" alt="">
      </div>
    </div>
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <span class="cdl-form-title"></span>
        <div class="cdl-form-cnt">
          <Select v-model="articleData.tag_id">
            <Option v-for="item in tagList" :value="item.tid" :key="item.tid">{{ item.tag_name }}</Option>
          </Select>
        </div>
      </div>
    </div>
    <div class="cdl-form-item">
      <mavon-editor
        ref="md"
        :value="articleData.markdownText"
        @imgAdd="imgAdd"
        :ishljs="true"
        @change="articleSave"
      ></mavon-editor>
    </div>
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <Button type="primary" :size="'large'" @click="send">{{send_btn_text}}</Button>
      </div>
    </div>
  </div>
</template>
<script>
import {
  addArticle,
  tagList as tagListReq,
  uploadImg,
  articleContentByAid
} from "@/server/server";
export default {
  data() {
    return {
      fileVal: "", // input[type="file"] 
      tagList: [], // 
      articleData: {
        aid: "",
        title: "", // 
        preface: "", // 
        cover: "", // 
        tag_id: "", // 
        markdownText: "", // 
        markdownHtml: "" // 
      },
      send_btn_text: ""
    };
  },
  beforeCreate() {
    // 
    tagListReq().then(({ d: tagList }) => {
      this.tagList = tagList;
    });
  },
  created() {
    // aid
    const aid = this.$route.params.aid;
    this.articleData.aid = aid;
    if (aid) {
      this.send_btn_text = "";
      // aid
      articleContentByAid(aid).then(({ d: articleInfo }) => {
        for (const key in articleInfo) {
          this.articleData[key] = articleInfo[key];
        }
      });
    }
  },
  watch: {
    $route(to, from) {
      for (const key in this.articleData) {
        this.articleData[key] = "";
      }
      if (to.name === "articleAdd") this.send_btn_text = "";
    }
  },
  methods: {
    // 
    uploadImg(e) {
      const loading = new c.Loading("~");
      const file = e.target.files[0];

      const formdata = new FormData();
      formdata.append("image", file);

      uploadImg(formdata)
        .then(({ d: data }) => {
          this.articleData.cover = data.src;
        })
        .finally(() => {
          loading.close();
        });
    },
    // 
    closeUploadImg() {
      this.articleData.cover = "";
      // input file
      this.fileVal = "";
    },
    // 
    articleSave(markdownText, markdownHtml) {
      this.articleData.markdownText = markdownText;
      this.articleData.markdownHtml = markdownHtml;
    },
    // markdown 
    imgAdd(pos, file) {
      const $mavon = this.$refs.md;
      const loading = new c.Loading("~");
      // 
      const formdata = new FormData();
      formdata.append("image", file);
      uploadImg(formdata)
        .then(data => {
          $mavon.$img2Url(pos, data.src);
        })
        .finally(() => {
          loading.close();
        });
    },
    // 
    send() {
      // loading
      const loading = new c.Loading(`${this.send_btn_text}`);
      addArticle(this.articleData)
        .then(data => {
          if (data.c === 0) this.$router.push({ path: "articleList" });
        })
        .finally(() => {
          loading.close();
        });
    }
  }
};
</script>
<style lang="less">
.markdown-body img {
  width: 300px !important;
}
</style>


